import React, { useCallback, useMemo, useState } from 'react';
import {
  Box, Button, FormControl, FormLabel, Input, Select
} from '@chakra-ui/react';
import { useTranslation } from 'react-i18next';
import { shallowEqual } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import MiniBox from '../../../layout/MiniBox';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import { wirePay } from '../../../store/middlewares/payment';
import { updateContact } from '../../../store/middlewares/contact';
import { apiErrorHandler } from '../../../utils/general';
import AddressSearch from '../../../components/AddressSearch';
import { useModuleData } from '../../../hooks/module';
import CurrencyInput from '../../../components/CurrencyInput';

const accountTypes = {
  personal: 'Personal',
  business: 'Business',
};

const DomesticWirePayment:React.FC = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const navigate = useNavigate();
  const { gmapKey } = useModuleData();

  const accountDetails = useAppSelector((store) => store.account.selectedAccount, shallowEqual);
  const contactDetails = useAppSelector((store) => store.contact.selectedContact, shallowEqual);
  const [localContactDetails, updateContactData] = useState({
    beneficiaryAddress: contactDetails?.wire?.international?.beneficiaryAddress,
    accountNumber: contactDetails?.wire?.international?.accountNumber || '',
    bankIdentifierCode: contactDetails?.wire?.international?.bankIdentifierCode || '',
    beneficiaryBank: contactDetails?.wire?.international?.beneficiaryBank || '',
    accountType: contactDetails?.wire?.international?.accountType || 'personal',
  });

  const [bankAddress, updateBankAddressState] = useState(contactDetails?.wire?.international?.beneficiaryBankAddress || {
    line1: '',
    line2: '',
    city: '',
    state: '',
    country: '',
    postalCode: '',
    addressType: 'legal',
  });

  const [paymentData, updatePaymentData] = useState<{
    accountId: string,
    contactId: string,
    amount: string,
    description: string
    type: 'international'
  }>({
    amount: '',
    description: '',
    contactId: contactDetails?.id || '',
    accountId: accountDetails?.id || '',
    type: 'international'
  });

  const isValid = useMemo(() => localContactDetails.accountNumber.length !== 0 && localContactDetails.bankIdentifierCode.length !== 0 && localContactDetails.beneficiaryBank.length !== 0 && paymentData.amount.length !== 0 && parseFloat(paymentData.amount) >= 0.01 && paymentData.description.length !== 0 && !!bankAddress
  && !!bankAddress.line1
  && !!bankAddress.city
  && !!bankAddress.state
  && !!bankAddress.country
  && !!bankAddress.postalCode
  && bankAddress.line1.length !== 0
  && bankAddress.city.length !== 0
  && bankAddress.state.length !== 0
  && bankAddress.country.length !== 0
  && bankAddress.postalCode.length !== 0, [localContactDetails, paymentData, bankAddress]);

  const onSubmit = useCallback(() => {
    if (contactDetails?.id && isValid) {
      dispatch(updateContact(contactDetails.id, {
        accountId: contactDetails.accountId,
        wire: {
          international: {
            beneficiaryAddress: localContactDetails.beneficiaryAddress,
            accountNumber: localContactDetails.accountNumber,
            bankIdentifierCode: localContactDetails.bankIdentifierCode,
            beneficiaryBank: localContactDetails.beneficiaryBank,
            accountType: localContactDetails.accountType,
            bankIdentifierType: 'Swift',
            beneficiaryBankAddress: bankAddress
          }
        }
      })).then(() => {
        dispatch(wirePay(paymentData))
          .then(() => {
            navigate('/payments/success');
          }).catch(apiErrorHandler);
      }).catch(apiErrorHandler);
    }
  }, [isValid, paymentData, localContactDetails, bankAddress]);

  return (
    <div className="page">
      <MiniBox
        heading={t('payments_international_wire.title')}
        footer={(
          <Button width="100%" variant="primary" disabled={!isValid} onClick={onSubmit}>
            {t('payments_international_wire.ctaText')}
          </Button>
)}
      >
        <Box width="100%" paddingTop="15px">

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_international_wire.accountNumber')}
            </FormLabel>
            <Input
              variant="primary"
              value={localContactDetails.accountNumber}
              onChange={(e) => {
                updateContactData({
                  ...localContactDetails,
                  accountNumber: e.target.value

                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_international_wire.code')}
            </FormLabel>
            <Input
              variant="primary"
              value={localContactDetails.bankIdentifierCode}
              onChange={(e) => {
                updateContactData({
                  ...localContactDetails,
                  bankIdentifierCode: e.target.value

                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_domestic_wire.accountType')}
            </FormLabel>
            <Select
              variant="primary"
              value={localContactDetails.accountType}
              onChange={(e) => {
                updateContactData({
                  ...localContactDetails,
                  accountType: e.target.value

                });
              }}
            >
              {Object.keys(accountTypes).map((a) => <option key={a} value={a}>{(accountTypes as any)[a]}</option>)}

            </Select>
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_international_wire.bank')}
            </FormLabel>
            <Input
              variant="primary"
              value={localContactDetails.beneficiaryBank}
              onChange={(e) => {
                updateContactData({
                  ...localContactDetails,
                  beneficiaryBank: e.target.value

                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_international_wire.address1')}
            </FormLabel>
            <AddressSearch
              placeholder={t('payments_international_wire.address1')}
              international
              googleApiKey={gmapKey}
              address={bankAddress}
              onChange={(address) => {
                updateBankAddressState({
                  ...bankAddress,
                  ...address,
                  addressType: 'legal'
                });
              }}
            />
            {/* <Input variant="primary" value="" /> */}
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_international_wire.address2')}
            </FormLabel>
            <Input
              variant="primary"
              placeholder={t('payments_international_wire.address2')}
              value={bankAddress?.line2}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                updateBankAddressState({
                  ...bankAddress,
                  line2: e.target.value,
                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_international_wire.addressCity')}
            </FormLabel>
            <Input
              variant="primary"
              placeholder={t('payments_international_wire.addressCity')}
              value={bankAddress?.city}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                updateBankAddressState({
                  ...bankAddress,
                  city: e.target.value,
                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_international_wire.addressState')}
            </FormLabel>
            <Input
              variant="primary"
              placeholder={t('payments_international_wire.addressState')}
              value={bankAddress?.state}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                updateBankAddressState({
                  ...bankAddress,
                  state: e.target.value,
                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_international_wire.addressZipcode')}
            </FormLabel>
            <Input
              variant="primary"
              placeholder={t('payments_international_wire.addressZipcode')}
              value={bankAddress?.postalCode}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                updateBankAddressState({
                  ...bankAddress,
                  postalCode: e.target.value,
                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_international_wire.addressCountry')}
            </FormLabel>
            <Input
              variant="primary"
              value={bankAddress?.country}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                updateBankAddressState({
                  ...bankAddress,
                  country: e.target.value,
                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_international_wire.amount')}
            </FormLabel>
            <CurrencyInput
              value={paymentData.amount}
              onChange={(value) => {
                updatePaymentData({
                  ...paymentData,
                  amount: value || ''
                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_international_wire.purpose')}
            </FormLabel>
            <Input
              variant="primary"
              value={paymentData.description}
              onChange={(e) => {
                updatePaymentData({
                  ...paymentData,
                  description: e.target.value
                });
              }}
            />
          </FormControl>
        </Box>
      </MiniBox>
    </div>
  );
};

export default DomesticWirePayment;
